import JsonExcel from "vue-json-excel";
import VideoPlayer from 'nuxt-video-player'
export default {
  layout: "user",
  components: {
    downloadexcel: JsonExcel,
    VideoPlayer
  },
  data() {
    return {
      overlay: false,
      isPaid: true,
      search: "",
    };
  },

  async fetch() {
    this.overlay = true;
    this.$store.dispatch("videos/getVideos");
    this.overlay = false;
  },
  computed: {
    videos() {
      const data = this.$store.state.videos.videos;
      if (this.search) {
        return data.filter(
          (dt) => !dt.vx_name.toUpperCase().indexOf(this.search.toUpperCase())
        );
      }
      return data;
    },
  },
  methods: {},
};
