export default {
  props: ["id"],
  data() {
    return {
      addPermModel: false,
      valid: false,
      form: {
        vx_name:'',
        vx_videourl:'',
      },
      vx_nameRule: [(v) => !!v || "required"],
       vx_videourlRule: [(v) => !!v || "required"],
      snackbar: false,
      color: "",
      text: "",
      loading: false,
    };
  },
  methods: {
    async submit() {
      if (this.$refs.form.validate()) {
        this.valid = true;
        this.loading = true;
        try {
          await this.$axios
            .post("api/admin/videos", this.form)
            .then((res) => {
              this.loading = false;
              this.color = "success";
              this.snackbar = true;
              this.text = res.data.message;
              this.$store.dispatch("videos/getVideos");
              this.$refs.form.reset();
              this.addPermModel = false;
            });
        } catch (err) {
          this.loading = false;
          this.color = "error";
          this.snackbar = true;
          this.text = err.response.data.message;
        }
      }
    },
  },
};
